@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css");
@import url("https://fonts.googleapis.com/css?family=Aleo");
@import url("https://cdnjs.cloudflare.com/ajax/libs/bttn.css/0.2.4/bttn.min.css");
@import url("https://cdnjs.cloudflare.com/ajax/libs/bttn.css/0.2.4/standalone/material-flat.min.css");

*,
body {
  margin: 0;
  padding: 0;
}
#wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 100vh;
  background: #005792;
  min-width: 350px;
  flex-wrap: wrap;
}
#pomodoro-box {
  width: 80%;
  min-width: 320px;
  text-align: center;
  font-family: "Aleo", serif;
  background-color: #edf9fc;
  border-radius: 3px;
  height: 350px;
  color: #005792;
}
button {
  color: white !important;
}

#cog-btn {
  float: right;
}

.hidden {
  display: none;
}
#config {
  padding: 20px;
}
#config button {
  margin: 10px;
}

#break,
#session {
  width: 42%;
  display: inline-block;
}

#break-label,
#session-label {
  font-size: 24px;
  padding: 20px;
}
#reset-container {
  width: 100%;
}
#reset {
  margin: 40px !important;
}

#timer {
  padding: 20px;
}
#timer-label {
  padding: 20px;
  font-size: 40px;
}
#time-left {
  padding: 20px;
  font-size: 72px;
  font-weight: bold;
}
#timer-buttons > button {
  margin: 10px;
  font-size: 16px !important;
}

@media screen and (min-width: 728px) {
  #timer-buttons > button {
    margin: 20px;
  }
  #pomodoro-box {
    width: 65%;
  }
}

@media screen and (min-width: 1024px) {
  #pomodoro-box {
    width: 52%;
  }
}
